
import {mapMutations} from "vuex";
import Msg from 'vux/src/components/msg/index.vue'

export default {
components:{
Msg
},
data(){
return{
buttons:[
{
text:"跳转至登录",
type:"primary",
link:"/login"
}
]
}
},
methods:{
...mapMutations(['mutationsMsgcount'])
},

created(){

if(this.$store.state.islogin==false){
return
}

window.localStorage.clear()

this.$store.commit("mutationsClear");
this.$store.commit("mutationsMsgcount","")

this.$router.push("/topiclist/all")

}
}



