import Vue from "vue"
import Vuex from "vuex"
import mutations from "./mutations"
import actions from "./actions"

Vue.use(Vuex);

const store=new Vuex.Store({
        state:{
            topiclist:[
                {name:"all",desc:"全部"},
                {name:"good",desc:"精华"},
                {name:"share",desc:"分享"},
                {name:"ask",desc:"问答"},
                {name:"job",desc:"招聘"},
                {name:"dev",desc:"测试"}
              ],
              isShow:false,//默认显示actionsheet菜单
              islogin:false,//默认显示未登录
              loginend:{
                center:"个人中心",
                msgs:"消息中心",
                create:"发表新话题",
                logout:"退出"
              },
              unlogin:{
                login:"登录",
                reginst:'注册',
                menus3:"已有账号,立即登录"
              },
              loginname:"", //保存登陆后的用户名
              accesstoken:"",  //保存登录后的令牌
              author_id:"" ,//保存用户的id
              mesCount:""

        },
        getters:{
            moreMeuns(state){
                //判断用户是否登录
                if(state.islogin){ //登录状态
                  return state.loginend  //登陆后的菜单
                }
                return state.unlogin  //未登录的菜单
              },
              //用来出来分类显示汉字
              filelist(state){
                return function(tab){
                  for (let i = 0; i < state.topiclist.length; i++) {
                    const element = state.topiclist[i];
                    if(element.name==tab){
                      return state.topiclist[i].desc
                    }
                  }
                }
              }
        },
        mutations,
        actions
})


export default store;



