
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components:{
XTextarea,
Group,
XButton
},
props:['id'],
data(){
return{
topics:{
title:"",
content:''
}

}
},
beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
vm.topics=res.data.data
}
})
})
},
beforeRouteUpdate(to,from,next){
var app=this
this.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
app.topics=res.data.data
}
})
},
methods:{
editopic(){
var app=this
this.$http.post("/topics/update",{
accesstoken:this.$store.state.accesstoken,
tab:"dev",
topic_id:this.topics.id,
title:this.topics.title,
content:this.topics.content
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.$router.back()
}
})
}
}
}



