
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import  {mapActions} from "vuex";
export default {
components:{
ButtonTab,
ButtonTabItem,
Cell,
Group,
Swipeout, SwipeoutItem, SwipeoutButton
},
data(){
return{
hasread:"", //已读消息
hasnotread:"",  //未读消息
selected:0
}
},
methods:{
...mapActions(['getMsgsCount']),

getReadMsgs(){
var app=this
this.$http.get("/messages",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data)
if(res.data && res.data.success){
app.hasread=res.data.data.has_read_messages
app.hasnotread=res.data.data.hasnot_read_messages
}
})
},
handleTab(index){
this.selected=index
},
markRead(make_id){
var app=this
this.$http.post("/message/mark_one/"+make_id,{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data)
if(res.data && res.data.success){
app.getMsgsCount()



var markMsgs;
for(var i=0;i<app.hasnotread.length;i++){
if(app.hasnotread[i].id==make_id){
markMsgs=app.hasnotread[i],
app.hasnotread.splice(i,1)
break
}
}
app.hasread.unshift(markMsgs)
}
})
},

markall(){
var app=this
this.$http.post("message/mark_all",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
if(res.data&& res.data.success){
app.$router.go(0)
}
})
}
},
created(){
this.getReadMsgs()
}
}



