
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
props:['loginname'],
components:{
Card,
Group,
Cell
},
data(){
return{
username:null,
collect:""
}
},
beforeRouteEnter(to,from,next){

next(function(vm){
vm.$http.get("/user/"+to.params.loginname).then(function(res){
vm.username=res.data.data
vm.username=res.data.data
});
vm.$http.get("/topic_collect/"+to.params.loginname).then(function(res){
vm.collect=res.data.data
})
})
},
beforeRouteUpdata(to,from,next){
var app=this
this.$http.get("/user/"+to.params.loginname).then(function(res){
app.username=res.data.data
});
this.$http.get("/topic_collect/"+to.params.loginname).then(function(res){
app.collect=res.data.data
})
next()
}
}



