
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapGetters, mapState}  from "vuex"
export default {
props:['name'],
components:{
Cell,
Group,
Scroller ,
Tab,
TabItem
},
computed:{
...mapState(["topiclist"]),
...mapGetters(["filelist"])
},
data:function(){
return{
listdata:[],
page:1, //默认是第一页
limit:20,//每页显示的数据个数
pullupConfig:{//设置上拉刷新配置信息
content:"上拉加载更多",
downContent:"松开加载更多",
upContent:"上拉加载更多",
loadingContent:"加载中"
},
pulldownConfig:{
content:"下拉刷新数据",
downContent:"松开刷新数据",
upContent:"下拉刷新数据",
loadingContent:"刷新中"
}
}
},
methods:{

refreshDate(){
var app=this;
this.$http.get("/topics",{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
console.log(res)
if(res.data.success){
app.listdata=res.data.data;

app.$refs.pullup.donePulldown()
app.page=1;
}
})
},
loadMoreDate(){

var app=this;
this.page=this.page+1
this.$http.get("/topics",{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
for(var i=0;i<res.data.data.length;i++){
app.listdata.push(res.data.data[i]);
}

app.$refs.pullup.donePullup()
}
})
},


handletab(index){
console.log(index)
this.$router.push("/topiclist/"+this.topiclist[index].name)
},
},


beforeRouteEnter(to,form,next){
next(function(vm){
vm.$http.get("/topics",{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){
vm.listdata=res.data;
if(res.data.success){
vm.listdata=res.data.data
}
})
})
},
beforeRouteUpdate(to,form,next){
var app=this;
app.$http.get("/topics",{
params:{
tab:to.params.name,
page:to.params.page,
limit:to.params.limit
}
}).then(function(res){
app.listdata=res.data;
if(res.data.success){
app.listdata=res.data.data
}
}),
next()
}
}



