import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../components/Home"
import Topiclist from "../components/Topiclist"
import Topic from "../components/Topic"
import Center from "../components/Center"
import Msgs from "../components/Msgs"
import Logout from "../components/Logout"
import Login from "../components/Login"
import User from "../components/User"
import Create from "../components/Create"
import Edite from "../components/Edite"
Vue.use(VueRouter);

const routes=[
  // {
  //   path:"/",
  //   component:Home
  // },
  //默认加载页面,全部分类的主题列表
  {
    path:"/",
    redirect:"/topiclist/all"
  },
  //主题列表 (对应分类加载)
  {
    path:"/Topiclist/:name",
    name:"topiclist",
    component:Topiclist,
    props:true
  },
  //主题详情(id值)
  {
    path:"/Topic/:id",
    name:"topic",
    component:Topic,
    props:true

  },
  {
    path:"/center",
    name:"center",
    component:Center,
  },
  {
    path:"/msgs",
    name:"msgs",
    component:Msgs,
  },
  {
    path:"/logout",
    name:"logout",
    component:Logout,
  },
  {
    path:"/login",
    name:"login",
    component:Login,
  },
  {
    path:"/user/:loginname",
    name:"user",
    component:User,
    props:true
  },
  {
    path:"/create",
    name:"create",
    component:Create,

  },
  {
    path:"/topic/:id/edit",
    name:"edite",
    component:Edite,
    props:true

  },
];

const router=new VueRouter({
  routes
})

export default router




