
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from "vuex"
export default {
props:['id'],
data(){
return{
topics:"",
reply_content:'',
reply_content2:"",
isshow:false , //默认不显示弹出框
reply_id:""
}
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem,
Card,
XTextarea,
XButton,
Popup
},
directives: {
TransferDom
},
computed:{
...mapState(['islogin'])
} ,

beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
vm.topics=res.data.data
}
})
})
},
beforeRouteUpdate(to,from,next){
var app=this
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:app.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
app.topics=res.data.data
}
})
},
methods:{

collect(){
var app=this;
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.topics.is_collect=true
}
})
},


decollect(){
var app=this;
this.$http.post("/topic_collect/de_collect ",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.topics.is_collect=false
}
})
},

addRepaly(replytype){
var app=this;
var params={
accesstoken:this.$store.state.accesstoken,
content:this.reply_content
}
if(replytype==2){
params.reply_id=this.reply_id
params.content=this.reply_content2
}
this.$http.post("/topic/"+this.topics.id+"/replies ",params).then(function(res){
console.log(res.data)
if(res.data.success){ //评论成功后手动加载评论内容
var reply={
"id": res.data.reply_id,  //返回评论的id
"author": {
"loginname": app.$store.state.loginname, //评论的用户名 和头像
"avatar_url": "https://avatars.githubusercontent.com/u/75511137?v=4&s=120"
},
"content": params.content,  //评论内容
"ups": [],
"create_at": "2021-05-27T02:23:49.807Z",
"reply_id": null,
"is_uped": false
}
app.topics.replies.push(reply)
app.reply_content=""  
app.isshow=false   
app.reply_content2=""
app.reply_id=""
}
})
},
handlepopup(reply_id,reply_loginname){
this.isshow=true
this.reply_content2='@'+reply_loginname+this.reply_content2
this.reply_id=reply_id
},
handlehide(){
this.reply_content2=""
},
upOrDown(reply_id){
var app=this
this.$http.post("/reply/"+reply_id+"/ups",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
if(res.data.success){
var reply; 
for(var i=0;i<app.topics.replies.length;i++){
if(app.topics.replies[i].id==reply_id){
reply=app.topics.replies[i]
break;
}
}
if(res.data.action=="up"){








reply.ups.push(app.$store.state.author_id);
}
if(res.data.action=="down"){








var index  =reply.ups.indexOf(app.$store.state.author_id);
console.log(index)
reply.ups.splice(index,1)
}
}
})
}

}
}



